.social-links {
  text-align: center;
}

@media only screen and (max-width: 640px) {
  .social-links {
    width: 100%;
  }
}

.social-links.relative {
  position: relative;
  bottom: 15px;
}

.social-links .logo {
  width: 60px;
  position: absolute;
  display: inline-block;
  vertical-align: middle;
  margin-right: 20px;
  float: left;
  margin-top: 10px;
}

@media only screen and (max-width: 640px) {
  .social-links .logo {
    display: block;
    margin: auto;
    float: none;
  }
}

.social-links .social-group {
  display: inline-block;
}

@media only screen and (max-width: 640px) {
  .social-links .social-group {
    display: block;
    width: 240px;
    margin: 25px auto;
  }
}

.social-links .social {
  vertical-align: middle;
  transition: transform 0.4s, color 0.4s;
  display: inline-block;
  font-size: 30px;
  width: 30px;
  color: #fff;
  text-align: center;
  text-decoration: none;
  margin: 10px 25px;
  transition: transform 0.4s, color 0.4s;
}

.social-links .social:hover {
  transform: scale(1.2);
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(359deg);
  }
}
